import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';



import { AppComponent } from './app.component';
import { ExampComponent } from './examp/examp.component';
import { Examp2Component } from './examp2/examp2.component';
import { TextinputComponent } from './textinput/textinput.component';
import { StructComponent } from './struct/struct.component';
import { PersonComponent } from './person/person.component';
import { ChildComponent } from './child/child.component';
import { VoteComponent } from './vote/vote.component';
import { ListvoteComponent } from './listvote/listvote.component';
import { ChildviewComponent } from './childview/childview.component';
import { ParentviewComponent } from './parentview/parentview.component';
import { NgcontentComponent } from './ngcontent/ngcontent.component';
import { Convertmd5Component } from './convertmd5/convertmd5.component';
import { ThoitietComponent } from './thoitiet/thoitiet.component';
import { NgformComponent } from './ngform/ngform.component';
import { ContactComponent } from './contact/contact.component';
import { NewsComponent } from './news/news.component';
const routesConfig: Routes = [
  {path: 'contact', component: ContactComponent},
  {path: 'news', component: NewsComponent}
];
@NgModule({
  declarations: [
    AppComponent,
    ExampComponent,
    Examp2Component,
    TextinputComponent,
    StructComponent,
    PersonComponent,
    ChildComponent,
    VoteComponent,
    ListvoteComponent,
    ChildviewComponent,
    ParentviewComponent,
    NgcontentComponent,
    Convertmd5Component,
    ThoitietComponent,
    NgformComponent,
    ContactComponent,
    NewsComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(routesConfig)
  ],
  providers: [],
  bootstrap: [AppComponent],
  exports:[RouterModule]
})
export class AppModule {
    birthday = new Date();
 }



