import { Component, OnInit } from '@angular/core';
import { IpService } from '../ip.service';
@Component({
  selector: 'app-convertmd5',
  templateUrl: './convertmd5.component.html',
  styleUrls: ['./convertmd5.component.css'],
  providers: [IpService]
})
export class Convertmd5Component implements OnInit {
  md5: String;
  constructor(private ipservice: IpService) { 
    
  }

  ngOnInit() {
  }
  convertmd5(){
    this.ipservice.getMd5()
    .then(md5 => this.md5 = md5);
  }
  

}



