import { Component, OnInit } from '@angular/core';
import { WeatherService } from '../weather.service';
@Component({
  selector: 'app-thoitiet',
  templateUrl: './thoitiet.component.html',
  styleUrls: ['./thoitiet.component.css'],
  providers: [WeatherService]
})
export class ThoitietComponent implements OnInit {

  constructor(private weather: WeatherService) { }

  ngOnInit() {
  }
  txtcity = '';
  result = null;
  isshow = false;
  
  getnhietdo(){
    if (this.txtcity === '')
      this.result = "Enter your city name";
    else
    this.weather.getTemp(this.txtcity).then(e => {
      this.result = "Nhiệt độ hiện tại ở "+this.txtcity+ " là: "+e;
      this.txtcity = '';
    })
    .catch(err => {
      this.result = "cannot find your city";
      this.txtcity ='';
    });
  }

}



