import { Component } from '@angular/core';
@Component({
    templateUrl: './examp.component.html',
    selector: 'app-examp',
    styleUrls: ['./examp.component.css'],
})
export class ExampComponent{
    birthday = new Date();
    en = "Hello";
    imgurl = "https://angular.io/assets/images/logos/angular/logo-nav@2x.png";
    forgot = false;
    toggleforgot(){
        this.forgot = !this.forgot;
    }
}


