import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-struct',
  templateUrl: './struct.component.html',
  styleUrls: ['./struct.component.css']
})
export class StructComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }
  Employees = ['Trung', 'Phúc', 'Nhạc'];
  Objects = [
    {id: 1, name: 'Trung', age: 18, active: true},
    {id: 2, name: 'Nhạc', age: 30, active: false},
    {id: 3, name: 'Phúc', age: 21, active: true}
  ];
  

  
  newName = '';
  newage = null;
  isShowform = false;
  filter = 'view_all';
  addObject(){
    this.Objects.push(
      {id: this.Objects.length +1, name: this.newName, age: this.newage, active : false }
    );
    this.newName = '';
    this.newage = null;
    this.isShowform = false;
  }
  removeObject(id:number){
    const index = this.Objects.findIndex(Object => Object.id === id);
    this.Objects.splice(index,1);
  }
  getShowObject(active: boolean){
    const view_all = this.filter === 'view_all';
    const view_active = this.filter  === 'view_active' && active;
    const view_noactive = this.filter  === 'view_noactive' && !active;
    return view_all || view_active || view_noactive;
  }
  changeActive(active: boolean){
    
  }
  

}



